<template>

    <div class="serial-pictures-reader">

        <section class="main" :data-theme="isDefaultTheme ? 'default' : 'dark'">
            <div class="menu">
                <span ref="titleRef" class="title">請選擇圖片資料夾：</span>
                <input type="file" @change="selectFolder" webkitdirectory mozdirectory msdirectory odirectory directory multiple>

                <!-- 說明 modal 按鈕 -->
                <button type="button" class="btn" data-bs-toggle="modal" data-bs-target="#staticBackdrop">
                    <span class="material-symbols-outlined">help</span>
                </button>

                <!-- 改變主題顏色按鈕 -->
                <button class="dark-mode-btn" @click="changeColorTheme">
                    <span class="material-symbols-outlined">contrast</span>
                </button>
            </div>
            <div class="panel">
                <img v-for="(item, index) in imgList" :key="index" 
                    :data-name="item.name" 
                    :src="item.src" />
            </div>
        </section>


        <!-- Modal -->
        <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h1 class="modal-title fs-5" id="staticBackdropLabel">使用說明</h1>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <p>漫畫圖檔名稱須為整齊含固定長度編號的名稱，例如：</p>
                        <ul>
                            <li>image_001.jpg</li>
                            <li>image_002.jpg</li>
                            <li>...</li>
                            <li>image_010.jpg</li>
                        </ul>
                        <br>
                        <p>
                            Step.1 點擊「選擇檔案」按鈕<br>
                            Step.2 選擇「漫畫單話」資料夾，例如：第一話<br>
                            Step.3 開啃
                        </p>
                    </div>
                    <!-- <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                        <button type="button" class="btn btn-primary">Understood</button>
                    </div> -->
                </div>
            </div>
        </div>


    </div>

</template>
<script>
import { ref, reactive } from 'vue';

export default {
    name: 'SerialPicturesReader',
    setup() {
        // 數據
        let isDefaultTheme = ref(true);
        let imgList = reactive([]);

        // 元素
        let titleRef = ref(null);


        // 方法

        // 改變顏色主題
        function changeColorTheme() {
            isDefaultTheme.value = !isDefaultTheme.value;
        }

        // 選擇資料夾
        function selectFolder(e) {
            let theFiles = e.target.files; // choose folder's all files
            
            // have folder
            if (theFiles.length > 0) {
                // remove images
                if( imgList.length != 0 ) {
                    imgList.length = 0;
                }

                // append images
                for (let i=0; i < theFiles.length ; i++) {
                    let reader = new FileReader();

                    reader.onload = function(e) {
                        imgList.push({
                            src: e.target.result,
                            name: theFiles[i].name,
                        })
                    }
                    
                    reader.readAsDataURL(theFiles[i]); // convert to base64 string
                }

                // update title
                let relativePath = theFiles[0].webkitRelativePath;
                let folder_name = relativePath.split("/")[0];
                titleRef.value.textContent = folder_name;
            }
        }


        return {
            isDefaultTheme,
            imgList,

            titleRef,

            changeColorTheme,
            selectFolder,
        }
    }
}
</script>